











import { Component, Vue } from 'vue-property-decorator';
import {Mutation, namespace} from 'vuex-class';
import Tabbar from './tabbar.vue';
import { SWITCH_TABBAR, Home, SHOW_TABBAR, HIDE_TABBAR } from '@/store/types';
import { HomePrevTab, HomeNextTab, HomeSetTab, ShowTabbar, HideTabbar } from '@/store/interface';

const HomeModule = namespace('Home');
@Component({
  components: {
    Tabbar,
  },
})
export default class Root extends Vue {
  @Mutation(SWITCH_TABBAR)
  private switchTabbar!: any;
  @Mutation(SHOW_TABBAR)
  private showTabbar!: ShowTabbar;
  @Mutation(HIDE_TABBAR)
  private hideTabbar!: HideTabbar;
  @HomeModule.Mutation(Home.PREV_TAB)
  private prevTab!: HomePrevTab;
  @HomeModule.Mutation(Home.NEXT_TAB)

  private nextTab!: HomeNextTab;
  private touch(down: boolean) {
    if (down) {
      this.showTabbar();
    } else {
      this.hideTabbar();
    }
  }
  private swipe(direction: 'Left' | 'Right') {
    // if (direction === 'Left') {
    //   this.nextTab();
    // } else {
    //   this.prevTab();
    // }
  }
}
